
import Image from 'next/image';

import styles from './page.module.scss';

export default function Example() {
  return (
    <div className={styles.wrapper}>
      <Image
        alt='trid1'
        src='/example/trid1.avif'
        width={980}
        height={566}
      />
      <Image
        alt='trid2'
        src='/example/trid2.avif'
        width={980}
        height={566}
      />
      <Image
        alt='trid3'
        src='/example/trid3.avif'
        width={980}
        height={566}
      />
    </div>
  );
}
